    box-sizing: border-box;
    padding: 0;
    margin: 0; 

}

/**** CSS variables *****/
:root{
    --main-color-1:#F7F5F3;
    --main-color-2: #EEE0CB;
    --main-color-3: #839788;
    --main-color-4: #000000;

}

@font-face {
  font-family: 'maPolice'; 
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  font-weight: 100;
}

#app {
    font-family: 'maPolice',Arial, Helvetica, sans-serif;
    height: 100vh;
    width: 100vw;
    color: var(--main-color-2);
    background-color: var(--main-color-3);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2em;
}

.title{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5em;
}

.board
{
    display: grid;
    grid-template-rows: repeat(8,minmax(2em,4em));
    grid-template-columns: repeat(8,minmax(2em,4em));
    border: 0.75em solid #7c6a06;
    border-radius: 10px;
    
    
}
.white
{
    background-color:var(--main-color-1) ;
    padding: 1em;
    color: #000000; 
    display: flex;
    justify-content: center;
    align-items: center;
}

.black
{
    background-color: var(--main-color-4);
    padding: 1em;
    color: #F7F5F3;
    display: flex;
    justify-content: center;
    align-items: center;
}
.start
{
    background-color: var(--main-color-3);
}
.gestion
{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black;
    gap: 1em;
}
.btn
{
    width: auto;
    height: auto;
    padding: 0.3em;
    line-height: none;
    background-color: var(--main-color-2);
    border-radius: 5px;
    font-family: "maPolice";
    font-size: 1em;
}


.knight-icon
{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-repeat: no-repeat;
    background-size:cover;
    padding: 0.3em;
    border: solid 3px #7c6a06;
    border-radius: 10px;
